import { Directive,ElementRef} from '@angular/core';
import { AnimationBuilder } from '@angular/platform-browser/src/animate/animation_builder';

@Directive({
  selector:'[downup-box]',
  host:{
    '[style.background-color]':"'red'"
  },
  exportAs:"downup"
})
export class DownupBox {
  constructor(private _db:AnimationBuilder,
              private _el:ElementRef){}
              
  toggle(isVisiable:boolean=false){
    let ani=this._db.css();
    ani.setDuration(1000);
    if(isVisiable){
      ani.setFromStyles({height:"0",width:"50%",overflow:"hidden"})
         .setToStyles({height:"10em"});
    }else{
      ani.setFromStyles({height:"10em",width:"50%"})
         .setToStyles({height:"0"});
    }
    ani.start(this._el.nativeElement);
  }
}